/* Navbar for Larger Screens */
@media (min-width: 900px) {
  .navbar-sm {
    display: none;
  }
  #toggle-menu {
    border-radius: 0 0 10px 10px;
    height: 70px;
    position: absolute;
    top: 0px;
    right: 10px;
    width: auto;
    padding: 0px 15px;
    display: flex;
    align-items: center;
    list-style: none;
  }

  #toggle-menu li a {
    font-family: "Nunito Sans", sans-serif;
    line-height: 1.5rem;
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-family: 800;
    padding: 0px 15px;
    border-radius: 0 0 10px 10px;
  }

  #toggle-menu li a:hover {
    transition: 0.3s all ease;
    color: white;
    padding: 7px 15px;
    background-color: crimson;
    border-radius: 5px 5px 5px 5px;
    box-shadow: 1px 2px 20px rgba(220, 20, 60, 0.2);
  }

  .toggle-btn {
    display: none;
  }
}

/* Navbar for Smaller screens */
@media (max-width: 900px) {
  #toggle-menu {
    display: none;
  }

  .dropdown > .active > a {
    background-color: crimson !important;
  }
  .dropdown-toggle {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 0.5px;
    left: 42vw;
    justify-content: space-evenly;
    align-items: right;
    text-align: center;
    width: 31px;
    height: 36px;
    cursor: pointer;
  }

  .dropdown-toggle .bar {
    height: 3px;
    width: 100%;
    background-color: black;
    border-radius: 30px;
  }

  .dropdown-item a {
    font-family: "Nunito", sans-serif;
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-family: 800;
    padding: 7px 15px;
    border-radius: 0 0 10px 10px;
  }

  .dropdown-item a:hover {
    transition: 0.3s all ease;
    color: white;
    padding: 5px 15px;
    background-color: crimson !important;
    border-radius: 10px;
    box-shadow: 1px 2px 20px rgba(220, 20, 60, 0.2);
  }

  .dropdown-item:active {
    transition: 0.3s all ease;
    padding: 5px 15px;
    background-color: transparent;
    color: black;
    border-radius: 10px;
  }

  .dropdown-toggle::after {
    display: none;
  }
  .navbar {
    display: none;
  }
}

