.loader {
  margin: auto;
  border: 20px solid none;
  border-radius: 50%;
  border-top: 20px solid #dc143c;
  display: flex;
  justify-self:center;
  height:20rem;
  width: 20rem;
  -webkit-animation: spinner 0.5s linear infinite;
          animation: spinner 0.5s linear infinite;
}

@-webkit-keyframes spinner {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spinner {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.projects {
  display: grid;
  grid-template-rows: 0.1fr 1fr;
  background-color: #111;
  background-color: rgba(220, 20, 60, 1);
  padding-top: 0px;
}

.projects h1 {
  text-align: center;
  margin: 25px 0px;
  padding-top: 10px;
  font-size: 42px;
  color: white;
  font-family: "Ibarra Real Nova", serif;
}

.projects-tiles {
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 360px));
  justify-content: space-around;
  align-items: center;
}

.card {
  width: 90%;
  height: 70%;
  padding: 2rem 1rem;
  background: #fff;
  position: relative;
  display: grid;
  box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.5);
  transition: all 0.5s ease-in-out;
  justify-self: center;
  align-self: center;
  margin: 25px auto;
}

.projects .card:hover {
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
}

.card:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: rgb(240, 125, 139);
  background: linear-gradient(
    180deg,
    rgba(220, 20, 60, 0.8) 0%,
    rgba(251, 251, 251, 1) 100%
  );
  /* background: linear-gradient(to bottom, rgba(0,176,155,0.5), rgba(150,201,61,1)); */
  z-index: 2;
  transition: all 0.5s;
  opacity: 0;
}

.projects-tiles .card:hover:before {
  opacity: 1;
}

.card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.card .info {
  position: relative;
  z-index: 3;
  color: black;
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  transition: 0.5s all;
  text-align: center;
  padding-top: 10%;
}

.card:hover .info {
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}

.card .info h1 {
  margin: 0;
}

.card .info p {
  letter-spacing: 1px;
  font-size: 15px;
  margin-top: 8px;
  margin-bottom: 20px;
}

